import React from "react";
import { useState, useRef, useEffect } from "react";

const XIVAPI = require("@xivapi/js");
const xiv = new XIVAPI();

const MarketTracker = ({ marketData }) => {
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 20;

  const startIdx = (currentPage - 1) * itemsPerPage;
  const paginatedData = marketData.slice(startIdx, startIdx + itemsPerPage);

  const handleNextPage = () => {
    if (currentPage < Math.ceil(marketData.length / itemsPerPage)) {
      setCurrentPage(currentPage + 1);
    }
  };

  const handlePreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  return (
    <div>
      <ul className="top-sale-list">
        {paginatedData.map((item, index) => (
          <li className="top-sale-item" key={index}>
            <div className="item-image">
              <img src={item.itemImage} alt={`Item: ${item.itemName}`} />
            </div>
            <div className="item-detail">
              <strong>Item Name: </strong> {item.itemName} <br />
              <strong>Item ID: </strong> {item.itemID} <br />
              <strong>NQ Sale Velocity: </strong> {item.nqSaleVelocity}
            </div>
          </li>
        ))}
      </ul>

      <div id="page-control">
        <button
          className="page"
          onClick={handlePreviousPage}
          disabled={currentPage === 1}
        >
          Previous
        </button>
        <button
          className="page"
          onClick={handleNextPage}
          disabled={currentPage === Math.ceil(marketData.length / itemsPerPage)}
        >
          Next
        </button>
      </div>
    </div>
  );
};

export default MarketTracker;
