// export default fetchItemDetails;

import buildItemIconUrl from "./buildItemIconUrl";

const fetchItemDetails = async (itemID) => {
  let itemName = "Unknown Item";
  let itemImageUrl = "defaultImageUrl";
  try {
    const response = await fetch(`https://xivapi.com/item/${itemID}`, {
      mode: "cors",
    });

    if (!response.ok) {
      console.error(
        `Failed to fetch data for item ${itemID}. Status: ${response.status}`
      );
    }

    const data = await response.json();

    // Check if the essential data (itemName and Icon) is available
    itemName = data.Name || "Unknown Item"; // Default to "Unknown Item" if name is missing
    itemImageUrl = data.Icon ? buildItemIconUrl(data.Icon) : "defaultImageUrl"; // Default image URL if Icon is missing

    return {
      itemName,
      itemImage: itemImageUrl,
    };
  } catch (error) {
    console.error(`Error fetching item details for ID ${itemID}:`, error);
  }
};

export default fetchItemDetails;
