import React, { useState } from "react";
import itemsID from "../data/parsedItems.json";
import buildItemIconUrl from "./buildItemIconUrl";
import worldName from "../data/dataCenter.json";

const CreateList = ({ dataCenter }) => {
  const [itemInput, setItemInput] = useState("");
  const [uploadedItems, setUploadedItems] = useState([]);
  const [searchResults, setSearchResults] = useState([]);
  const [itemDetails, setItemDetails] = useState([]);

  const handleInputChange = (e) => {
    setItemInput(e.target.value);
  };

  const handleItemSearch = () => {
    const itemNames = itemInput.split(",").map((name) => name.trim());
    setUploadedItems(itemNames);
    searchAndFetchItems(uploadedItems);
  };

  const findWorldName = (worldName, worldID) => {
    const world = worldName.find((entry) => entry.id === worldID);
    return world ? world.name : "World ID not found";
  };

  const findItemKeyByName = (itemsID, itemName) => {
    if (!itemName) {
      console.error("Error: itemName is null or undefined");
      return null;
    } else {
      const foundItem = itemsID.find(
        (item) => item.Name === itemName.toLowerCase()
      );
      return foundItem ? foundItem.key : null;
    }
  };

  const fetchItemImageAndDetails = async (key) => {
    try {
      const itemResponse = await fetch(`https://xivapi.com/item/${key}`, {
        mode: "cors",
      });
      const itemData = await itemResponse.json();
      const itemImageUrl = buildItemIconUrl(itemData.Icon);

      const marketResponse = await fetch(
        `https://universalis.app/api/v2/aggregated/${dataCenter}/${key}`
      );
      const marketData = await marketResponse.json();

      const { itemId } = marketData.results[0];
      const minListingPrice = marketData.results[0].nq.minListing.region.price;
      const worldID = marketData.results[0].nq.minListing.region.worldId;
      const world = findWorldName(worldName, worldID);

      return {
        itemId,
        itemName: itemData.Name,
        itemImage: itemImageUrl,
        minListingPrice,
        worldID,
        world,
      };
    } catch (error) {
      console.error(`Error fetching item details for ID ${key}: `, error);
      return {
        itemId: key,
        error: "Failed to fetch item details",
      };
    }
  };

  const searchAndFetchItems = async (itemNames) => {
    const foundItems = itemNames.map((itemName) => ({
      name: itemName,
      key: findItemKeyByName(itemsID, itemName),
    }));
    setSearchResults(foundItems);

    const detailsPromises = foundItems.map(async (item) => {
      if (item.key) {
        return await fetchItemImageAndDetails(item.key, item.key);
      } else {
        return {
          name: item.name,
          error: "Item Key not found",
        };
      }
    });

    const details = await Promise.all(detailsPromises);
    setItemDetails(details);
  };

  const groupedByWorld = itemDetails.reduce((acc, detail) => {
    if (!detail.worldID) return acc;
    if (!acc[detail.worldID]) {
      acc[detail.worldID] = [];
    }
    acc[detail.worldID].push(detail);
    return acc;
  }, {});

  return (
    <div>
      <h2>Create Your Own List</h2>
      <div className="items-input">
        <textarea
          className="file-upload"
          type="text"
          value={itemInput}
          onChange={handleInputChange}
          placeholder="Enter item names here, separated by commas:"
        />
        <button className="file-upload" onClick={handleItemSearch}>
          Search and Fetch Items
        </button>
      </div>

      <ul className="top-sale-list">
        <h2>Current Lowest Price by World</h2>
        {Object.keys(groupedByWorld).map((worldID) => (
          <div key={worldID}>
            <h3>World: {findWorldName(worldName, Number(worldID))}</h3>
            <ul>
              {groupedByWorld[worldID].map((detail, index) => (
                <li className="top-sale-item" key={index}>
                  {detail.error ? (
                    <span>
                      {detail.name}: {detail.error}
                    </span>
                  ) : (
                    <div className="list-data">
                      <div className="item-image">
                        <img
                          src={detail.itemImage}
                          alt={`Item: ${detail.itemName}`}
                        />
                      </div>

                      <div className="item-detail">
                        <strong>Item ID: </strong> {detail.itemId} <br />
                        <strong>Item Name: </strong> {detail.itemName} <br />
                        <strong>Price: </strong> {detail.minListingPrice} <br />
                      </div>
                    </div>
                  )}
                </li>
              ))}
            </ul>
          </div>
        ))}
        {/* {itemDetails.map((detail, index) => (
          <li className="top-sale-item" key={index}>
            {detail.error ? (
              <span>
                {detail.name}: {detail.error}
              </span>
            ) : (
              <div className="list-data">
                <div className="item-image">
                  <img
                    src={detail.itemImage}
                    alt={`Item: ${detail.itemName}`}
                  />
                </div>
                <div className="item-detail">
                  <strong>Item ID: </strong> {detail.itemId} <br />
                  <strong>Item Name: </strong> {detail.itemName} <br />
                  <strong>Price: </strong> {detail.minListingPrice} <br />
                  <strong>World: </strong> {detail.world}
                </div>
              </div>
            )}
          </li>
        ))} */}
      </ul>
    </div>
  );
};

export default CreateList;
