import React, { useState, useRef, useEffect } from "react";
import MarketTracker from "./MarketTracker";
import CreateList from "./CreateList";
import titleimg from "../img/title.png";
import loadingimg from "../img/loading.gif";
import fetchItemDetails from "./fetchItemDetails";

const MainComponent = () => {
  const [view, setView] = useState("marketData");
  const [marketData, setMarketData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [dataCenter, setDataCenter] = useState("Oceania");

  const marketableItemsIDsRef = useRef([]);
  const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  // Re-fetch market data when dataCenter changes
  useEffect(() => {
    if (marketableItemsIDsRef.current.length > 0) {
      setLoading(true); // Set loading when data fetching starts
      fetchMarketData(); // Fetch market data based on new dataCenter
    }
  }, [dataCenter]);

  useEffect(() => {
    const fetchMarketableItems = async () => {
      try {
        const response = await fetch(
          "https://universalis.app/api/v2/marketable"
        );
        const marketableItemsIDs = await response.json();

        // Save fetched marketable items to useRef
        marketableItemsIDsRef.current = marketableItemsIDs;

        // Trigger fetching market data once marketable items are fetched
        fetchMarketData();
      } catch (error) {
        console.log("Error fetching marketable item IDs: ", error);
        setLoading(false);
      }
    };

    // Fetch marketable items only once on component mount
    fetchMarketableItems();
  }, []);

  const fetchMarketData = async () => {
    const itemIDs = marketableItemsIDsRef.current; // Use the fetched item IDs
    if (itemIDs.length === 0) return; // Avoid fetching if itemIDs are not ready
    const allData = [];
    const batchSize = 100;

    try {
      const promises = [];
      for (let i = 0; i < itemIDs.length; i += batchSize) {
        const batch = itemIDs.slice(i, i + batchSize);
        const joinedBatch = batch.join(",");

        const fetchBatchData = async () => {
          try {
            const response = await fetch(
              `https://universalis.app/api/v2/history/${dataCenter}/${joinedBatch}`
            );

            if (!response.ok) {
              if (response.status === 429) {
                console.error(
                  "Error 429: Too Many Requests. Skipping this batch."
                );
                return null;
              } else {
                throw new Error(
                  `Error: ${response.status} ${response.statusText}`
                );
              }
            }

            const data = await response.json();

            return data;
          } catch (error) {
            console.error("Error fetching batch data: ", error);
            return null;
          }
        };

        promises.push(fetchBatchData());
        await delay(200);
      }

      const batchData = await Promise.all(promises);

      batchData.forEach((data) => {
        if (data && typeof data === "object" && data.items) {
          const items = Object.values(data.items).map((item) => ({
            itemID: item.itemID,
            nqSaleVelocity: item.nqSaleVelocity,
          }));
          allData.push(...items);
        } else {
          console.log(
            "Received non-object data or missing items property: ",
            data
          );
        }
      });

      const sortedData = allData
        .sort((a, b) => b.nqSaleVelocity - a.nqSaleVelocity)
        .slice(0, 100);
      const itemDetails = await fetchItemDetailsInBatches(sortedData, 20, 500);
      setMarketData(itemDetails);
    } catch (error) {
      console.error("Error fetching item history: ", error);
    } finally {
      setLoading(false);
    }
  };

  const fetchItemDetailsInBatches = async (
    items,
    batchSize = 5,
    delayMs = 500
  ) => {
    const allDetails = [];
    for (let i = 0; i < items.length; i += batchSize) {
      const batch = items.slice(i, i + batchSize);
      const itemDetailsPromises = batch.map(async (item) => {
        const details = await fetchItemDetails(item.itemID);
        return {
          itemID: item.itemID,
          nqSaleVelocity: item.nqSaleVelocity,
          itemName: details.itemName,
          itemImage: details.itemImage,
        };
      });

      const batchResults = await Promise.all(itemDetailsPromises);
      allDetails.push(...batchResults);

      if (i + batchSize < items.length) {
        await delay(delayMs);
      }
    }
    return allDetails;
  };

  if (loading) {
    return (
      <div>
        <img src={loadingimg} alt="Loading..." />
        <h1>Fetching Data from Universalis API...</h1>
      </div>
    );
  }

  return (
    <div>
      <img
        id="title-image"
        src={titleimg}
        alt="title:Top Selling Items in FFXIV Market"
      />
      <div className="option">
        <div id="data-center-selection">
          <p>Data Center:</p>
          <select
            id="data-center"
            value={dataCenter}
            onChange={(e) => setDataCenter(e.target.value)}
          >
            {["Oceania", "Japan", "Europe", "North-America", "China"].map(
              (dc) => (
                <option id="data-center-option" key={dc} value={dc}>
                  {dc}
                </option>
              )
            )}
          </select>
        </div>
        <div className="content-control">
          <div className="content-option">
            <img
              src="https://xivapi.com/img-misc/home_menu_marketpurchase.png"
              alt="Current Market Data"
              className="option-image"
              onClick={() => setView("marketData")}
            />
            <p>Current Market Data</p>
          </div>
          <div className="content-option">
            <img
              src="https://xivapi.com/img-misc/home_menu_address.png"
              alt="Create Your Own List"
              className="option-image"
              onClick={() => setView("createList")}
            />
            <p>Create Your Own List</p>
          </div>
        </div>
      </div>

      {view === "marketData" ? (
        <MarketTracker marketData={marketData} />
      ) : (
        <CreateList
          dataCenter={dataCenter}
          marketableItemsIDs={marketableItemsIDsRef}
        />
      )}
    </div>
  );
};

export default MainComponent;
